import { useContext } from 'react';
import './history.css'
import { GlobalState } from '../../contexts/globalstate';

const History = () => {
    const {transactions,del}=useContext(GlobalState)
    return (<>
            <h3>History</h3>
        <ul className="list">
            {transactions.map((transaction) => 
            <li key={transaction.id} className={parseInt(transaction.amount)>0?'plus':'minus'}>
                {transaction.text}
                <span>{transaction.amount}</span>
                <button onClick={()=>del(transaction.id)} className="delete-btnz">X</button>
            </li>
            )}
        </ul>

    </>);
}
 
export default History;