import './balance.css'
import { GlobalState } from '../../contexts/globalstate';
import { useContext } from 'react';
const Balance = () => {
    const { transactions } = useContext(GlobalState)
    let showBalance=0
    if (transactions.length !== 0) {
        showBalance=true
    }
    return (<>
        <h4>Your Balance</h4>
        <h1>${showBalance && (transactions.map((item)=>parseFloat(item.amount,2)).reduce((prev,neww)=>prev+neww))}</h1>

        <div className="inc-exp-container">
            <div>
                <h4>Income</h4>
                <p className="money plus">+${showBalance && (transactions.map((item) => item.amount > 0 &&
                    parseFloat(item.amount, 2)).reduce((prev, neww) => prev + neww))}</p>
            </div>
            <div>
                <h4>Expense</h4>
                <p className="money minus">-${showBalance && (transactions.map((item) => item.amount < 0 &&
                    parseFloat(item.amount, 2)).reduce((prev, neww) => prev + neww)*-1)}</p>
            </div>
        </div>

    </>);
}
 
export default Balance;