import { v4 } from 'uuid'
import {bake_cookie} from 'sfcookies'
export const GlobalReducer = (state, action) => {
    let newtrans = null;
    switch (action.type) {
        case "ADD":
            newtrans = [...state, { id: v4(), text: action.text, amount: action.amount }]
            bake_cookie('transactions',newtrans)
            return newtrans
        case "DELETE":
            newtrans = state.filter((item) => item.id !== action.id)
            bake_cookie('transactions',newtrans)
            return newtrans
        default:
            return state
    }
}